.leftMenu {
  width: 50px;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background: #073c3c;
  z-index: 9999999;
}
.leftMenu #menu {
  width: 80vh;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  /* height: 100%; */
  transform: rotate(-90deg);
  transform-origin: bottom left;
  position: absolute;
  bottom: 0;
  left: 33px;
}
.leftMenu #menu li {
  display: block;
  position: relative;
}
.leftMenu #menu li.active a {
  color: #e49028;
}
.leftMenu #menu li a {
  font-size: 17px;
  color: #9bd3d3;
  padding: 15px 20px;
  width: 100%;
  text-decoration: none;
  white-space: nowrap;
}

.logowrap {
  width: 100%;
  padding: 10px 3px;
  background: fixed;
  background: #e49028;
  display: block;
  line-height: 0;
}
.logowrap img {
  width: 100%;
}

.main {
  width: 50%;
  float: right;
}
.main .section {
  background: #073c3c;
}

.leftSection {
  width: 50%;
  height: 100%;
  padding-left: 50px;
  float: left;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.leftSection .mianlogowarap {
  width: 50%;
