}
.leftSection .wrapsecin {
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0 25px;
  width: 100%;
}
.leftSection #countdown-gampang {
  width: 100%;
}

#countdown-gampang {
  width: 100%;
}

.ClassyCountdown-wrapper {
  display: flex;
  justify-content: center;
}
.ClassyCountdown-wrapper > div {
  margin: 20px;
  position: relative;
  height: 136px;
  width: 136px;
}
.ClassyCountdown-wrapper > div .ClassyCountdown-value {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-top: 0 !important;
  line-height: 1;
  text-align: center;
  line-height: 0.6;
}
.ClassyCountdown-wrapper > div .ClassyCountdown-value span {
  font-size: 16px !important;
}